import React, { useState, useMemo } from 'react';
import Button from '@material-ui/core/Button';
import Container from '@material-ui/core/Container';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';

import AnkiResult from './Result';

import { useAppSelector, useAppDispatch } from '../app/hooks';
import { selectSettings } from '../features/settings/settingsSlice';
import { selectQuestions } from '../features/questions/questionsSlice';
import { logUpdate } from '../features/answerLog/answerLogSlice';
import {
  sessionInit,
  answerSelected,
  selectSession,
  questionMoved,
  selectSessionQuesNum,
} from '../features/session/sessionSlice';

import { Switch, Route, useRouteMatch, useHistory } from 'react-router-dom';

export const HISTORY_KEY = 'ANKI_WEB_HISTORY';

const useStyles = makeStyles(() => ({
  home: {
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
    padding: '20px',
  },
  nav: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-around',
    padding: '20px',
  },
  afterAnswer: {
    color: 'green',
  },
}));

export default function AnkiTraining() {
  const classes = useStyles();
  let { path, url } = useRouteMatch();
  let history = useHistory();
  const dispatch = useAppDispatch();

  // questions
  const questions = useAppSelector(selectQuestions);

  // session
  const session = useAppSelector(selectSession);
  const quesNum = useAppSelector(selectSessionQuesNum);

  // settings
  const settings = useAppSelector(selectSettings);

  // question now
  const question = useMemo(() => questions[quesNum], [questions, quesNum]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (settings.tapMode === 'tapMode') {
      handleAnsweredTrue();
    }

    dispatch(answerSelected({ key: question.questionId, val: e.target.value }));
  };

  // control answer
  const [answered, setAnswered] = useState(false);

  const handleAnsweredTrue = () => {
    setAnswered(true);
  };

  const handleAnsweredFalse = (nextNum: number) => {
    setAnswered(false);
    dispatch(questionMoved(nextNum));
  };

  // control move
  const handleMoveNext = () => {
    handleAnsweredFalse(quesNum + 1);
  };

  const handleMovePrev = () => {
    handleAnsweredFalse(quesNum - 1);
  };

  const handleResult = () => {
    handleAnsweredFalse(0);

    dispatch(
      logUpdate({
        questions,
        answers: session.selectedAnswers,
      })
    );

    dispatch(sessionInit());

    history.push(`${url}/result`);
  };

  return (
    <>
      <Switch>
        <Route exact path={path}>
          <Container>
            {
              // ÂïèÈ°åË°®Á§∫Ê¨Ñ
            }
            <Container maxWidth='md' className={classes.home}>
              <Box>ID: {question.questionId}</Box>
              <p style={{ whiteSpace: 'pre-line' }}>
                {question.questionText.replaceAll('\\n', '\n')}
              </p>
            </Container>

            <Container maxWidth='md' className={classes.home}>
              <FormControl component='fieldset'>
                <RadioGroup
                  aria-label='choicesRadio'
                  name='choicesRadio'
                  value={String(session.selectedAnswers[question.questionId])}
                  onChange={handleChange}
                >
                  {
                    // ÈÅ∏ÊäûËÇ¢„Çí‰∏Ä„Å§„Åö„Å§ÁîüÊàê
                    question.choices.map((e) => (
                      <FormControlLabel
                        key={e.choiceId}
                        value={String(e.choiceId)}
                        control={<Radio />}
                        label={e.choiceText}
                        className={answered ? classes.afterAnswer : undefined}
                      />
                    ))
                  }
                </RadioGroup>
              </FormControl>
            </Container>

            {
              // Ê≠£Á≠îË°®Á§∫/Ëß£Ë™¨Ë°®Á§∫Ê¨Ñ
            }
            <Container maxWidth='md' className={classes.home}>
              {settings.tapMode === 'buttonMode' && (
                <Button
                  variant='contained'
                  color='primary'
                  onClick={handleAnsweredTrue}
                >
                  Ê≠£Á≠î
                </Button>
              )}
              {answered &&
              String(session.selectedAnswers[question.questionId]) ===
                String(question.answer) ? (
                <p>Ê≠£Ëß£„Åß„Åô!üéâ</p>
              ) : answered ? (
                <p>‰∏çÊ≠£Ëß£„Åß„Åô„ÄÇ</p>
              ) : null}
              {answered && (
                <div style={{ whiteSpace: 'pre-line' }}>
                  <p>Ëß£Ë™¨</p>
                  <p>{question.desc.replaceAll('\\n', '\n')}</p>
                </div>
              )}
            </Container>

            {
              // „Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥Ê¨Ñ
            }
            <Container maxWidth='md' className={classes.nav}>
              {quesNum !== 0 && (
                <Button
                  variant='contained'
                  color='primary'
                  onClick={handleMovePrev}
                >
                  Ââç„ÅÆÂïèÈ°å
                </Button>
              )}
              {quesNum !== questions.length - 1 && (
                <Button
                  variant='contained'
                  color='primary'
                  onClick={handleMoveNext}
                >
                  Ê¨°„ÅÆÂïèÈ°å
                </Button>
              )}
              {quesNum === questions.length - 1 && (
                <Button
                  variant='contained'
                  color='secondary'
                  onClick={handleResult}
                >
                  ÂõûÁ≠îÁµÇ‰∫Ü
                </Button>
              )}
            </Container>
          </Container>
        </Route>
        <Route path={`${url}/result`}>
          <AnkiResult />
        </Route>
      </Switch>
    </>
  );
}
